import { deleteUserCard, getLikesCard, deleteLikeCard } from './api.js';


const cardTemplate = document.querySelector('#card-template').content;


export function createCard(cardData, userId, openPopupImage) {
  const cardElement = cardTemplate.querySelector('.places__item').cloneNode(true);
  const cardImage = cardElement.querySelector('.card__image');
  const cardTitle = cardElement.querySelector('.card__title');
  const deleteButton = cardElement.querySelector('.card__delete-button');
  const cardLikeButton = cardElement.querySelector('.card__like-button');
  const cardLikeValue = cardElement.querySelector('.card__like-value');
  
  cardElement.dataset.cardId = cardData._id;
  cardTitle.textContent = cardData.name;
  cardImage.src = cardData.link;
  cardImage.alt = cardData.name;
  

  if (cardData.likes.length) {
    cardLikeValue.textContent = cardData.likes.length;
  };

  if (cardData.likes.some((like) => like._id === userId)) {
    cardLikeButton.classList.add("card__like-button_is-active");
  };


  if (cardData.owner._id === userId) {
    deleteButton.classList.add('card__delete-button_active')
    deleteButton.addEventListener("click", function (evt) {
      const newCard = evt.target.closest('.card');
      deleteCard(newCard);
    }); 
  } else {
    deleteButton.remove();
  };
  

  cardLikeButton.addEventListener('click', () =>  addLikeCard(cardData, userId, cardElement));


  cardImage.addEventListener('click', () => openPopupImage);


  return cardElement;
};


export const deleteCard = (cardElement) => {
  deleteUserCard(cardElement.dataset.cardId)
    .then(() => {
      cardElement.remove();
    })
    .catch((error) => {
      console.log(error);
    });
};


export function addLikeCard(cardData, userId, cardElement) {
  const cardLikeButton = cardElement.querySelector('.card__like-button');
  const cardLikeValue = cardElement.querySelector('.card__like-value');
  if(checkLikeById(cardData, userId)){
    deleteLikeCard(cardData)
      .then(res => {
        cardLikeValue.textContent = res.likes.length;
        cardLikeButton.classList.remove('card__like-button_is-active');
      })
      .catch((error) => {
        console.log(error)
      })
  } else {
    getLikesCard(cardData)
      .then(res => {
        cardLikeValue.textContent = res.likes.length;
        cardLikeButton.classList.add('card__like-button_is-active');
      })
      .catch((error) => {
        console.log(error)
      })
  }
};


function checkLikeById(id, array) {
  const idArray = array.map(cardData => cardData._id)
  return idArray.includes(id)
};
